<template>
    <div>
        <h2>{{_id? '':''}}</h2>
        <el-form label-width='120px' >
            <el-form-item label='' >
                 <el-select v-model="model.categories" placeholder="" label-width='120px' multiple>
                    <el-option
                        v-for="item in categories"
                        :key="item._id"
                        :label="item.name"
                        :value="item._id">
                    </el-option>
                </el-select>
            </el-form-item>
            <el-form-item label='' >
                <el-input v-model="model.title"></el-input>
            </el-form-item>
            <el-form-item label=''>
                <quill-editor v-model="model.body"
                    ref="myQuillEditor"
                    :options='editorOption'
                >
                </quill-editor>
            </el-form-item>
            <el-form-item>
                <el-button type='primary' @click="save"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script type="text/ecmascript-6">
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'
import { quillEditor } from 'vue-quill-editor'
import {quillRedefine} from 'vue-quill-editor-upload'
    export default {
        name: "ArticleEdit",
        data() {
            return{
                model: {},
                categories: [],
                editorOption: {}
            }
        },
        components:{
            quillEditor
        },
        created() {
            this._id && this.fatch();
            this.getCategoriesData();
            this.editorOption = quillRedefine(
                {
                // 
                    uploadConfig: {
                        action: this.$http.defaults.baseURL + '/uploads',
                        res: (respnse) => {
                            return respnse.url;
                        },
                        methods: 'POST',
                        name: 'file',
                        accept: 'image/png, image/gif, image/jpeg, image/bmp, image/x-icon',
                    },
                }
            )
        },
        props: ['_id'],
        methods: {
            async save(){
                if(this._id){ //
                    await this.$http.put(`/rest/articles/${this._id}`,this.model)
                }else{ //
                    let res = await this.$http.post('/rest/articles',this.model);
                }
                this.$router.push('/articles/list');
                this.$message({
                    type: 'success',
                    message: ''
                })
            },
            async fatch(){ //
                let res = await this.$http.get(`/rest/articles/${this._id}`)
                this.model = res.data;
            },
            getCategoriesData(){
                this.$http.get('/rest/categories').then(res=>{
                    this.categories = res.data;
                })
            }
        },
    }
</script>

<style lang="stylus" scoped ref="stylesheet/stylus">

</style>